import logo from './logo.svg';
import './App.css';
import './Header/Header';
import React, { Component } from 'react';
import Header from './Header/Header';
import Menu from './Menu/Menu';

class App extends Component {

  state = {
    menuIsOpen : true,
  }
  closeMenu = () => {
    this.setState({
      menuIsOpen: false,
    });
  }
  openMenu = () => {
    this.setState({
      menuIsOpen: true,
    });
  }

  render() {
    const app = (
      <div className="App">
        <Header />
        {
          this.state.menuIsOpen && <Menu onClose={this.closeMenu} />
        }
        <p>Hello World</p>
      </div>
    );
    return app;
  }
}

export default App;
